/* Scoped styles for the table wrapper */
.table {
    /* Optional: width or margins if needed */
}

/* Style the AntD header row */
.table :global(.ant-table-thead > tr > th) {
    background-color: #003D8F;
    color: white;
    font-weight: 600;
}

/* Style table body text */
.table :global(.ant-table-tbody > tr > td) {
    font-size: 14px;
}

/* Alternating row backgrounds */
.evenRow :global(td) {
    background-color: #ffffff !important;
}

.oddRow :global(td) {
    background-color: #f9f9f9 !important;
}
