.container {
    background-color: #fff;
    border-radius: 8px;
    padding: 1.5rem;
    margin-bottom: 2rem;
}

.header {
    margin-bottom: 1rem;
}

.title {
    font-size: 1.25rem;
    font-weight: bold;
    color: #003D8F; /* Blue default, or switch to orange below */
    margin: 0;
}

.subtitle {
    font-size: 0.95rem;
    color: #666;
    margin-top: 0.25rem;
}

.chart {
    height: 400px;
}
